 #portfolio{
     position: relative;
     /* margin-top:3rem; */
     /* height: 100vh; */
     margin-right: 16px;
     margin-left: 16px;
     }
 .portfolio__container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    margin-top: 2rem;
 }
 .portfolio__item{
     width: 29.33%;
     text-align: center;
     margin-top: 15px;
     background: var( --color-primary-variant);
     padding: 10px;
     margin: 15px;
     border-radius: 2rem;
     border: 1px solid transparent;
     transition: var(--transition);
 }
 portfolio__item:hover{
     border-color: var(--color-primary-variant);
     background: transparent;
 }
 .portfolio__item-image{
     border-radius: 1.5rem;
     overflow: hidden;
 }
 .portfolio__item-cta{
     gap: 1.5rem;
     display: flex;
     justify-content: center;
 }
 .portfolio__item h3{
    font-size: 1rem;
    margin: 2rem auto;
 }