#contact{
    /* height: 100vh; */
    width: 100%;
    position: relative;
    /* margin: 0 5rem; */
    margin: 5rem 0;
}
.contact__container{
     display: flex;
     /* flex-direction: column; */

}
.contact__opttions{
    width: 50%;
     display: flex;
    flex-wrap: wrap;
    justify-content: flex-start; 
}
.contact__form{
    width: 40%;
}
.contact_option{
    /* float: right; */
     width: 40%;
    margin: 20px;
    background:  var(--color-primary-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var( --transition);

}
.contact_option:hover{
    background: transparent;
    border-color:var(--color-primary-variant) ;
}
.contact_option-icons {
    font-size: 1.5rem;
    margin-bottom:0.5rem ;
}
.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}
form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}
input,textarea{                                                                             
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var( --color-white);
}
