.experience__container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 3rem;
    margin-right: 4rem;
    margin-left: 4rem;
}
.experience__language_dev,.experience__tagcanvas{
        width: 50%;
}
.experience__language_dev > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius:  2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.experience__language_dev > div:hover{
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}
.experience__frontend{
    margin-bottom: 2rem;
}
.experience__language_dev > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 1rem; 
}
.experience__details {
    display:flex;
    gap:1rem;
}
.experience__details-icons {
    margin-top: 6px;
    color: var(--color-primary);
}
@media screen and (max-width: 600px) {
    .experience__language_dev,.experience__tagcanvas{
        width: 100%;
        text-align: center;
    }
}